body {
  background-color: whitesmoke;
}

header {
  background: #003E73;
  text-align: center;
  margin: 0 -15px 30px -15px;
}

header > p {
  color: whitesmoke;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 30px;
  line-height: 40px;
  padding: 10px;
}

.middle {
  max-width: 400px;
  margin: auto;
}

input,
button,
select {
  padding: 0.6em;
  margin-bottom: 0.6em;
  border: 1px solid #ccc;
  border-radius: 2px;
}

button {
  border: none;
  font-size: 20px;
  letter-spacing: 1.5px;
  color: white;
  background: #003E73;
}

.label {
  display: block;
  padding-right: 5px;
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 5px;
}

.steps-container {
  margin-bottom: 20px;
}

footer {
  text-align: center;
}

footer span {
  font-size: 15px;
}